@import "styles/variables.scss";

.container {
  display: flex;
  align-items: center;
  border-radius: 15px;
  border: 1px solid rgba(149, 150, 245, 0.2);
  padding: 15px;
  margin-top: 45px;

  span {
    color: $textColor2;
    flex: 1;
    font-size: 13px;
    font-weight: 300;
    line-height: 2;
  }

  .button {
    color: $textColor1;
    background-color: $red;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 50px;
    padding: 0 10px;
    margin-right: 15px;
    border-radius: 15px;
    text-align: center;
    font-size: 12px;
    font-weight: 500;
    cursor: pointer;
  }
}
