@import 'styles/variables.scss';

.container {
  background-color: $backgroundColor2;
  border-radius: 10px;
  padding: 0 15px;
  margin-top: 20px;

  .tabs {
    display: flex;
    padding-top: 10px;
    border-bottom: 1px solid rgba(101, 102, 176, 0.5019607843137255);

    .tab {
      flex: 0.25;
      color: $textColor3;
      padding-bottom: 10px;
      text-align: center;
      font-size: 12px;
      font-weight: 300;

      span {
        margin-bottom: 10px;
        padding-bottom: 10px;
      }

      &.active {
        span {
          color: $green;
          border-bottom: 2px solid $green;
          width: 65%;
        }
      }
    }
  }

  .header {
    display: flex;
    padding: 10px 0;

    .heading {
      flex: 1;
      color: $textColor3;
      font-size: 10px;

      &:last-child {
        flex: initial;
      }
    }
  }

  .body {
    .row {
      color: $textColor1;
      display: flex;
      align-items: center;
      padding: 10px 0 15px;

      & > div {
        flex: 1;

        &:last-child {
          flex: initial;
        }
      }

      .description {
        display: flex;
        align-items: center;

        img {
          width: 25px;
          height: 25px;
          margin-left: 5px;
        }

        .name {
          display: flex;
          flex-direction: column;

          .title {
            font-size: 14px;
          }

          .symbol {
            color: $textColor3;
            font-size: 10px;
          }
        }
      }

      .price {
        font-feature-settings: 'ss02';
        font-size: 12px;
		font-weight: 300;
      }

      .changes {
        background-color: #f84960;
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        direction: ltr;
        border-radius: 10px;
        height: 30px;
        width: 80px;
        font-feature-settings: 'ss02';
		font-size: 12px;
		font-weight: 300;
      }
    }
  }
}
