.carousel.carousel-slider {
  overflow: initial;
}

.carousel .control-dots {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 10px;
  bottom: -30px;
}

.carousel .control-dots .dot {
  width: 5px;
  height: 5px;
  margin: 0 2.5px;
}

.slider-wrapper {
  border-radius: 15px;
  cursor: pointer;
}
