@import "styles/variables.scss";

.container {
  display: flex;
  align-items: center;
  height: 60px;
  padding: 0 20px;

  h1 {
    color: $textColor1;
    flex: 1;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
  }

  .icon {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 10px;
    cursor: pointer;

    &:first-child {
      padding-right: 10px;
    }

    img {
      width: 24px;
      height: 24px;

      &.logo {
        filter: drop-shadow(0px 0px 8px $primaryColor);
      }
    }
  }
}
