@import "styles/variables.scss";

.container {
  background-color: $backgroundColor2;
  display: flex;
  padding: 15px 0;
  margin-top: 20px;
  border-radius: 10px;

  .item {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    span {
      color: $textColor1;
      margin-top: 8px;
      font-size: 10px;
      font-weight: 500;
    }

    img {
      width: 24px;
    }
  }
}
