@import 'styles/variables.scss';

.container {
  background-color: rgba(0, 2, 49, 0.5019607843137255);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 10px;
  right: 10px;
  height: 70px;
  z-index: 1001;
  width: calc(100% - 20px);
  border-radius: 20px;
  border: 1px solid #1d1e65;
  backdrop-filter: blur(30px);

  .item {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &.active {
      img {
        filter: invert(73%) sepia(34%) saturate(874%) hue-rotate(76deg)
          brightness(210%) contrast(97%);
      }
	  
      span {
        color: #48fb6f;
      }
    }

    span {
      color: $textColor1;
      margin-top: 8px;
      font-size: 10px;
      font-weight: 500;
    }

    img {
      width: 24px;
    }
  }
}
